  z-index: 10;
}
.d-loading.small i {
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
}
.d-loading i {
  position: absolute;
  left: calc(50% - 17px);
  top: calc(50% - 17px);
  width: 34px;
  height: 34px;
  border: 2px solid transparent;
  border-top-color: #26c;
  border-radius: 50%;
  animation: spin 0.75s infinite linear;
}
.d-loading i:before {
  content: "";
  top: -2px;
  left: -2px;
  position: absolute;
  width: inherit;
  height: inherit;
  border: inherit;
  border-radius: inherit;
  animation: spin 1.5s infinite ease;
}

