  stroke: #26c;
  fill: #26c;
}
.category-icon-inline:hover i, .category-icon-inline:hover svg {
  animation: slideUpShorter 0.6s;
}

@keyframes slideUpShorter {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px);
  }
  100% {
    transform: translateY(0);
  }
}
