/* -------------------------------------------
    Counter
---------------------------------------------- */
.count-to {
  display: inline-block;
  margin-bottom: 1.6rem;
  font-size: 5rem;
  font-weight: 700;
  line-height: 1;
}
.count-to::after {
  content: "+";
}

.count-title {
  color: #222;
  font-weight: 600;
  line-height: 1.4em;
  margin-bottom: 0.7rem;
  font-size: 2rem;
}

.counter-descri {
  line-height: 1.86em;
  margin-bottom: 0;
}

.count-float:after {
  content: "K+";
}

.symbol-dollar::after {
  display: none;
}
.symbol-dollar::before {
  content: "$";
}

.symbol-percent:after {
  content: "%";
}

.count-queue {
  display: flex;
  justify-content: center;
  align-items: center;
}

.queue-score .count-to::after {
  content: "";
}
.queue-score .count-to:not(:last-child)::after {
  display: inline-block;
  content: ":";
  width: 32px;
}

.queue-days .count-to::after {
  content: "";
}
