  position: absolute;
  width: 90px;
  height: 70px;
  left: 50%;
  top: 1.2rem;
  background-image: url(../images/elements.png);
  transform: translateX(-50%);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}
