/* -------------------------------------------
    Form
---------------------------------------------- */
.form .form-control {
  margin-bottom: 2rem;
}

.form-control {
  display: block;
  width: 100%;
  min-height: 4.5rem;
  padding: 0.85rem 2rem;
  border: 1px solid #e3e3e3;
  font-size: 1.4rem;
  line-height: 1.5;
  font-weight: 400;
  color: #666;
  background-color: transparent;
  border-radius: 0;
  box-shadow: none;
  outline: 0;
}
.form-control::placeholder {
  color: inherit;
  transition: color 0.3s;
}
.form-control::-webkit-input-placeholder {
  color: inherit;
  transition: color 0.3s;
}
.form-control::-moz-placeholder {
  color: inherit;
  transition: color 0.3s;
}
.form-control:focus::placeholder {
  color: transparent;
}
.form-control:focus::-webkit-input-placeholder {
  color: transparent;
}
.form-control:focus::-moz-placeholder {
  color: transparent;
}
.form-control.form-solid {
  background-color: #fff;
  border: 0;
  color: #8d8d8d;
}

textarea {
  font-family: inherit;
  min-width: 100%;
  max-width: 100%;
}

.custom-checkbox {
  position: absolute;
  opacity: 0;
}
.custom-checkbox + label {
  display: block;
  position: relative;
  padding-left: 2.4rem;
  cursor: pointer;
}
.custom-checkbox + label::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 18px;
  height: 18px;
  line-height: 17px;
  border: 1px solid #ebebeb;
  border-radius: 2px;
}
@media (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .custom-checkbox + label::before {
    line-height: 14px;
  }
}
.custom-checkbox:checked + label::before {
  content: "\f00c";
  border-color: #222;
  background: #222;
  color: #fff;
  font-size: 9px;
  font-weight: 600;
  font-family: "Font Awesome 5 Free";
  text-align: center;
}

.select-box,
.select-menu {
  position: relative;
}
.select-box select,
.select-menu select {
  position: relative;
  max-width: 14rem;
  min-height: auto;
  width: auto;
  height: 100%;
  padding-left: 9px;
  padding-right: 25px;
  border: 1px solid #d7d7d7;
  border-radius: 2px;
  color: inherit;
  background-color: transparent;
  font-size: 1.2rem;
  font-family: inherit;
  letter-spacing: inherit;
  z-index: 1;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.select-box select:focus,
.select-menu select:focus {
  outline: none;
}
.select-box::before,
.select-menu::before {
  content: "\f078";
  font-family: "Font Awesome 5 Free";
  position: absolute;
  font-weight: 900;
  font-size: 9px;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
}

.select-box option {
  font-size: 1.2rem;
}

@media (-ms-high-contrast: active), (-ms-high-contrast: none) {
  select::-ms-expand {
    display: none;
  }
  select:focus::-ms-value {
    background: transparent;
    color: currentColor;
  }
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.quantity {
  -moz-appearance: textfield;
  max-width: 48px;
}

.input-group {
  display: inline-flex;
  width: 11rem;
  height: 4.5rem;
}
.input-group .form-control {
  flex: 1;
  padding: 0;
  height: inherit;
  font-size: 1.4rem;
  border: solid #ebebeb;
  border-width: 1px 0;
  color: #222;
  text-align: center;
}
.input-group button {
  padding: 0;
  width: 3.1rem;
  border: 1px solid #ebebeb;
  font-size: 1.2rem;
  color: #222;
  background-color: transparent;
  cursor: pointer;
  -webkit-appearance: none;
}

.custom-radio {
  position: relative;
}
.custom-radio input {
  display: none;
}

input[type=radio] ~ label {
  padding-left: 2.5rem;
}
input[type=radio] ~ label::before, input[type=radio] ~ label::after {
  position: absolute;
  content: "";
  display: inline-block;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}
input[type=radio] ~ label::before {
  left: 0;
  width: 1.8rem;
  height: 1.8rem;
  background-color: #fff;
  border: 1px solid #cdcdcd;
}
input[type=radio] ~ label::after {
  width: 0.8rem;
  height: 0.8rem;
  left: 0.5rem;
  background: #666;
  opacity: 0;
