.overlay-light .banner,
.overlay-effect1 figure,
.overlay-effect1 .banner,
.overlay-effect2 figure,
.overlay-effect2 .banner,
.overlay-effect3 figure,
.overlay-effect3 .banner,
.overlay-effect4 figure,
.overlay-effect4 .banner {
