  animation: snow 10s linear infinite;
  z-index: 1;
}

@keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  50% {
    background-position: 500px 500px, 100px 300px, -200px 250px;
  }
  100% {
    background-position: 400px 800px, 300px 600px, -200px 400px;
  }
}
