.mfp-wrap {
  z-index: 3000;
}

.mfp-bg {
  z-index: 3000;
  background: #000;
  opacity: 0;
  transition: opacity 0.3s ease-out;
}
.mfp-bg.mfp-ready {
  opacity: 0.7;
}
.mfp-bg.mfp-removing {
  opacity: 0;
}

.mfp-container {
  padding: 4rem 2rem;
}
.mfp-container video {
  width: 100%;
  height: 100%;
}

.mfp-close {
  transform: rotateZ(45deg);
  transition: transform 0.3s;
  width: 18px;
  height: 18px;
  opacity: 1;
}
.mfp-wrap .mfp-close {
  top: 20px;
  right: 20px;
}
.mfp-content .mfp-close {
  top: -25px;
  right: 0;
}
.mfp-image-holder .mfp-close, .mfp-iframe-holder .mfp-close {
  top: 15px;
  right: 0;
}
.mfp-close span {
  display: none;
}
.mfp-close::before, .mfp-close::after {
  content: "";
  display: block;
  position: absolute;
  background-color: #fff;
  width: 2px;
  height: 100%;
  top: 0;
  left: calc(50% - 1px);
}
.mfp-close::before {
  width: 100%;
  height: 2px;
  top: calc(50% - 1px);
  left: 0;
}
.mfp-close:hover, .mfp-close:focus {
  transform: rotateZ(135deg) scale(1.5);
}

.pswp__button--arrow--left,
.pswp__button--arrow--right,
.mfp-arrow {
  width: 4.8rem;
  height: 4.8rem;
  color: #fff;
