  border: 2px solid;
}

.pswp__ui > button::before {
  background: transparent;
  position: static;
  line-height: 44px;
}
.pswp__ui > button.pswp__button--arrow--left::before {
  margin-right: 3px;
}
.pswp__ui > button.pswp__button--arrow--right::before {
  margin-left: 3px;
}
.pswp__ui > button:hover {
  opacity: 1;
}

.pswp__button--arrow--right::before {
  content: "\e950";
  font-family: "riode";
}

.mfp-arrow::before,
.mfp-arrow::after {
  content: "\f054";
  position: static;
  display: inline;
  margin: 0;
  border: 0;
}

.mfp-arrow::after {
  content: none;
}

button.mfp-arrow {
