.hotspot {
  position: relative;
}
.hotspot > a {
  display: flex;
  width: 4rem;
  height: 4rem;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: background-color 0.35s, color 0.35s;
}
.hotspot > a:before {
  font-family: "Font Awesome 5 Free";
  font-size: 1.4rem;
  font-weight: 900;
  color: #fff;
}
.hotspot.hotspotspread > a:after {
  content: "";
  position: absolute;
  background-color: #fff;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  z-index: -1;
  animation: hotspot-animate 2s infinite;
}
.hotspot:after {
  position: absolute;
  display: block;
  content: "";
  height: 100%;
  width: 100%;
}
.hotspot:hover .tooltip {
  visibility: visible;
  opacity: 1;
}
.hotspot.hotspot-type1 > a {
  background-color: #222;
}
.hotspot.hotspot-type1 > a:before {
  content: "\f067";
}
.hotspot.hotspot-type1:hover > a {
  background-color: #26c;
}
.hotspot.hotspot-type2 > a {
  background-color: #222;
}
.hotspot.hotspot-type2 > a:before {
  content: "\f111";
}
.hotspot.hotspot-type2:hover > a {
  background-color: #26c;
}
.hotspot.hotspot-type3 > a {
  background-color: #222;
}
.hotspot.hotspot-type3 > a:before {
  content: "\f111";
  font-weight: 400;
}
.hotspot.hotspot-type3:hover > a {
  background-color: #26c;
}
.hotspot.hotspot-type4 > a:before {
  content: "\f067";
  color: #222;
}
.hotspot.hotspot-type4:hover > a:before {
  color: #26c;
}
@keyframes spotFlash {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.hotspot.hotspot-style-flash > a {
  animation-name: spotFlash;
  animation-iteration-count: infinite;
  animation-duration: 3s;
}

.tooltip {
  position: absolute;
  display: block;
  padding: 1rem;
  background-color: #fff;
  visibility: hidden;
  opacity: 0;
  width: 240px;
  transition: opacity 0.3s;
  box-shadow: 0 5px 14px 0 rgba(155, 155, 155, 0.1);
  z-index: 11;
}
.tooltip .tooltip-name {
  font-size: 1.4rem;
