/********************************
        popup code-console
********************************/
.show-code {
  position: absolute;
  padding: 3px 10px;
  visibility: hidden;
  opacity: 0;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -10px);
  z-index: 100;
  border-radius: 3px;
  color: #fff;
  background-color: #26c;
  overflow: hidden;
  transition: visibility 0.35s, opacity 0.35s, transform 0.35s;
}

.code-template {
  position: relative;
}
.code-template .show-code > i {
  font-size: 14px;
}
.code-template .code-style {
  display: none;
}
.code-template:hover .show-code {
  cursor: pointer;
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, 0);
}

.code-popup {
  border-radius: 5px;
  width: 1000px;
  max-width: 100%;
  z-index: 4000;
}

.code-console {
  border-radius: 5px;
}
.code-console .CodeMirror {
  background-color: #f4f4f4;
  padding: 2rem 1rem 1rem 2rem;
  height: 580px;
  max-height: 80vh;
  color: #666;
}
.code-console .CodeMirror-vscrollbar::-webkit-scrollbar {
  width: 5px;
}
.code-console .CodeMirror-vscrollbar::-webkit-scrollbar-thumb {
  margin-right: 2px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}
.code-console .CodeMirror-hscrollbar::-webkit-scrollbar {
  height: 5px;
}
.code-console .CodeMirror-hscrollbar::-webkit-scrollbar-thumb {
  margin-bottom: 2px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}
.code-console .code-text {
  display: block !important;
  opacity: 0;
  padding: 0;
  border: none;
  height: 1px;
}
.code-console .tooltiptext {
  padding: 9px;
